.button {
    height: 58px;
    border-radius: 8px;
    font-weight: 500;
    background: #835afd;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border: 0;
    transition: filter 0.2s;
    padding: 0 32px;

    img {
        margin-right: 8px;
    }
    
    &.outlined {
        background: #FFF;
        border: 1px solid #835afd;
        color: #835afd;
    }

    &:not(:disabled):hover {
        filter: brightness(0.9);
    }
    &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
    }
}